
.gwc__byline {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid black;
    align-items: center;
    
}

.gwc__byline-top {
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    
}
.gwc__byline-top h1 {
    margin-bottom: -1rem;
    margin-top: 1.5rem;
    font-size: 20px;
}
.gwc__byline-top p {
    font-size: 15px;
}

.gwc__byline img {
    align-self: center;
    border-radius: 40px;
    width: 8%;
    height: 8%;
}

@media screen and (max-width: 414px) {
    .gwc__byline p {
        font-size: 13px;
        line-height: 1rem;
    }
    .gwc__byline h1 {
        font-size: 18px;
        line-height: 2rem;
    }
    .gwc__byline img {
        width: 15%;
        height: 30%;
    }
}