nav {
    position: fixed;
    background: rgb(251,251,251);
    background: linear-gradient(135deg, rgba(251,251,251,1) 0%, rgba(255,255,255,1) 59%);
    left: 0;
    right: 0;
    display: flex;
    padding: 0.5rem 4rem;
    box-shadow: 0px 3px 7.5px rgba(0, 0 ,0, 0.1);
    align-items: center;
    margin: 0;
    top: 0;
}

.gwc__navbar-heading {
    padding-right: 4rem;
    justify-self: start;
}
.gwc__navbar-heading a {
    text-decoration: none;
    color: black;
}
.gwc__navbar-links {
    flex: 1;
    display: flex;
    align-items: center;
}

.gwc__navbar-links a {
    font-size: 17px;
    cursor: pointer;
    display: block;
    color: black;
    font-weight: bold;
    text-align: center;
    padding: 0rem 4rem;
    text-decoration: none;
}

.gwc__navbar-button {
    font-weight: bold;
    border-radius: 5px;
    font-size: 16px;
    border: 0;
    margin: 0;
    float: right;
    outline: none;
    cursor: pointer;
    padding: 0.8rem 2rem;
}

.gwc__navbar-menu_container p {
    color: black;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    cursor: pointer;
    margin: 1.5rem 0;

}

.gwc__navbar-menu_container a {
    text-decoration: none;
    color: black;
}

.gwc__navbar-menu_container a:hover {
    text-decoration: underline;
}

.gwc__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gwc__navbar-menu svg {
    cursor: pointer;
    right: 0rem;
    color: black;
    float: right;
}

.gwc__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: start;
    background-color: rgb(247, 247, 247);
    padding: 2rem 2rem;
    position: absolute;
    right: -1.5rem;
    margin-top: 4.2rem;
    min-width: 150px;
    border-bottom-left-radius: 10px;
    box-shadow: 0px 0px 7.5px rgba(0, 0, 0, 0.2);
}

.gwc__navbar-menu_container:hover {
    box-shadow: 0px 0px 7.5px rgba(0, 0, 0, 0.5);
}


.gwc__navbar-right {
    display: flex;
    align-items: center;
    margin-right: -2rem;
    margin-left: auto;
}

@media screen and (max-width: 1400px) {
    .gwc__navbar-menu {
        display: flex;
    }
    .gwc__navbar-links {
        display: none;
    }
}

@media screen and (max-width: 414px) {
    .gwc__navbar-right {
        margin-right: -2rem;
    }
    nav {
        padding-left: 2rem;
        position: fixed;
        overflow-x: none;
    }
    .gwc__navbar-menu_container {
        padding: 0rem 1rem;
        min-width: 130px;
        right: -0.3rem;
        top: -0.3rem;
    }
}