
.gwc__published {
    padding-bottom: 2rem;
}
.gwc__published p {
    font-size: 16px;
}

.gwc__published-content {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid black;
    align-items: center;
    
}

.gwc__published-content_top {
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    
}
.gwc__published-content_top h1 {
    margin-bottom: -1rem;
    margin-top: 1.5rem;
    font-size: 20px;
}
.gwc__published-content_top p {
    font-size: 15px;
}

.gwc__published img {
    border-radius: 40px;
    width: 8%;
    height: 8%;
}

@media screen and (max-width: 414px) {
    .gwc__published p {
        font-size: 13px;
        line-height: 1rem;
    }
    .gwc__published h1 {
        font-size: 18px;
        line-height: 2rem;
    }
    .gwc__published img {
        width: 15%;
        height: 30%;
    }
}