.cards {
    display: flex;
    gap: 50px;
    margin: 3rem 9rem;
    padding: 1rem 1rem;
    overflow-x: scroll;
}

.gwc__widget-container {
    height: 42rem;
    width: 38rem;
    cursor: pointer;
    padding: 5rem 5rem;
    border-radius: 25px;
    box-shadow: 0px 3px 7.5px rgba(0, 0 ,0, 0.2);
    font-size: 12px;       /* ADD */
    display: flex;          /* ADD */
    flex-direction: column; /* ADD */
}
.gwc__widget-container:hover {
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.4);
}

.gwc__widget-container_image {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 1rem;
}

/* ADD */
.gwc__widget-container_title {
    margin: 0rem;
    margin-top: 1rem;
    font-size: 25px;
}

.gwc__widget-container_description {
    margin: 0rem;
    margin-top: 1rem;
    font-size: 18px;
    min-height: 110px;
}

.gwc__widget-container_button {
    padding: 1rem 0rem;
}

.gwc__widget-container_button button {
    font-size: 18px;
    padding: 0rem 2rem;
    border-radius: 8px;
    border: none;
    background-color: rgb(241, 241, 241);
    cursor: pointer;
    font-weight: bold;
    display: flex;
    float: right;
}

.gwc__widget-container_button button:hover {
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.4);
}

.gwc__widget-container a {
    color: black;
    text-decoration: none;
}

@media screen and (max-width: 1400px) {
    .cards {
        display: flex;
        flex-direction: row;
        margin: 3rem 4rem;
        
    }
    .gwc__widget-container {
        min-height: 40rem;
        cursor: pointer;
        padding: 4rem 4rem;
    }
    .gwc__widget-container_title {
        font-size: 22px;
        margin-top: 0rem;
    }
    .gwc__widget-container_description {
        font-size: 18px;
    }
}

@media screen and (max-width: 700px) {
    .cards {
        overflow-x:scroll;
        margin: 3rem 1rem;
        gap: 2rem;
    }
    .gwc__widget-container {
        min-height: auto;
        cursor: pointer;
        padding: 2rem 2rem;
        width: 14rem;
        height: 25rem;
    }
    .gwc__widget a {
        color: #000000
    }
    .gwc__widget-container_title {
        font-size: 17px;
        margin-bottom: -0.5rem;
        margin-top: 0rem;
    }
    .gwc__widget-container_description {
        font-size: 13px;
        margin-top: 0rem;
        height: 7.4rem;
        overflow: hidden;
    }
    .gwc__widget-container_button {
        position: relative;
        padding: 0rem 0rem;
        margin-bottom: 0rem;
        margin-top: 0rem;

    }
    .gwc__widget-container_button button {
        position: absolute;
        right: 0rem;
        bottom: 0rem;
        font-size: 16px;
        padding: 0rem 1rem;
    }
    
}