
.gwc__info-container {
    margin: 10rem 10rem;
    margin-bottom: 0rem;
    padding: 5rem 8rem;
    text-align: center;
}

.gwc__info-container h1 {
    font-size: 40px;
}

.gwc__info-container p {
    font-size: 30px;
}

@media screen and (max-width: 414px) {
    .gwc__info-container {
        margin: 0rem 0rem;
        margin-top: 6rem;
        padding: 1rem 1rem;
        text-align: center;
    }
    .gwc__info-container h1 {
        font-size: 30px;
    }
    .gwc__info-container p {
        font-size: 24px;
    }
}