.gwc__header {
    margin: 0rem;
}

.gwc__header-container {
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.2);
    margin: 10rem 10rem;
    margin-top: 5rem;
    margin-bottom: 0rem;
    border-radius: 25px;
    padding: 5rem 8rem;
    padding-top: 3rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gwc__header-container:hover {
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.4);
}

.gwc__header-container img {
    width: 50%;
    border-radius: 15px;
    margin-top: 1rem;
}

.gwc__header-container_text {
    display: flex;
    margin-right: 8rem;
    justify-content: center;
    flex-direction: column;
}
.gwc__header-container_text p {
    font-size: 22px;
    margin-top: 0rem;
}
.gwc__header-container_text button {
    font-size: 20px;
    margin-top: 1rem;
    padding: 1rem 2rem;
    border-radius: 5px;
    border: none;
    background-color: rgb(241, 241, 241);
    cursor: pointer;
    font-weight: bold;
    display: flex;
    flex-direction: row;
    align-self: flex-end;
}

.gwc__header-container_text button:hover {
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.4);
}

@media screen and (max-width: 1400px) {
    .gwc__header-container {
        box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.2);
        margin: 5rem 5rem;
        margin-top: 5rem;
        margin-bottom: 0rem;
        border-radius: 25px;
        padding: 5rem 8rem;
        padding-top: 3rem;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
    }
}

@media screen and (max-width: 414px) {
    .gwc__header-container {
        box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.2);
        margin: 2rem 2rem;
        margin-bottom: 0rem;
        border-radius: 25px;
        padding: 1rem 2rem;
    }
    .gwc__header-container_text h1 {
        font-size: 20px;
    }
    .gwc__header-container_text p {
        font-size: 16px;
    }
    .gwc__header-container_text {
        margin: 0.5rem 0rem;

    }
    .gwc__header-container img {
        display: none;
    }
    .gwc__header-container_text button {
        font-size: 18px;
    }
}